.navigbar {
	position:fixed;
	height: var(--nav-size);
	width:100%;
	background-color: var(--bg);
	padding: 0 1rem;
	border-bottom: var(--border);
}

.navigbar-navig {
	list-style: none;
	padding: 0;
	margin: 0;
	max-width: 100%;
	height: 100%;
	display:flex;
	justify-content: center;
}

.navig-item {
	width: calc(var(--nav-size) * 1);
	display: flex;
	align-items: center;
	justify-content: center;
}

.header-button {
  --button-size: calc(var(--nav-size) * 0.5);
  width: var(--button-size);
  height: var(--button-size);
  border-radius: 100%;
  padding: 5px;
  margin: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: filter 300ms;
  color:lightgrey;
  font-size: large;
}

.header-button:hover{
  color:white;
}

/* Small screens */
@media only screen and (max-width: 600px) {
  .navigbar {
    background-color: blue;
  }
}